* {
  padding: 0;
  margin: 0;
}

.navContainer {
  position: -webkit-sticky;
  position: sticky;
  top: 0;
  left: 0;
  z-index: 100 !important;
}

.title {
  display: flex;
  position: absolute;
  width: 55px;
  height: 55px;
  top: 5px;
  left: 50px;
}

.nav-list {
  display: flex;
  justify-content: center;
  align-items: center;
  text-decoration: none;
  list-style: none;
}

.nav-link {
  text-transform: uppercase;
  font-family: 'Varela Round', sans-serif;
  color: #fff;
  padding-bottom: 5px;
}

.navigation {
  height: 10vh;
  background-color: #222831;
  width: 100%;
  text-align: center;
  display: flex;
  justify-content: center;
  align-items: center;
  align-self: center;
  margin: 0 auto;
  transition: all 0.5s ease-in-out;
}

.nav-link:hover {
  color: white;
}

.nav-link {
  display: inline-block;
  position: relative;
  text-decoration: none;
  /* padding-right: 15px; */
}

.home, .server, .user {
  margin-right: 15px;
}

.nav-link:after {
  content: '';
  position: absolute;
  width: 100%;
  -webkit-transform: scaleX(0);
          transform: scaleX(0);
  height: 2px;
  bottom: 0;
  left: 0;
  background-color: #f8f8f8;
  -webkit-transform-origin: bottom right;
          transform-origin: bottom right;
  transition: -webkit-transform 0.25s ease-out;
  transition: transform 0.25s ease-out;
  transition: transform 0.25s ease-out, -webkit-transform 0.25s ease-out;
}

.nav-link:hover:after {
  -webkit-transform: scaleX(1);
          transform: scaleX(1);
  -webkit-transform-origin: bottom left;
          transform-origin: bottom left;
}

#open-menu {
  display: none;
}

#close-menu {
  opacity: 0;
  pointer-events: none;
  z-index: -1 !important;
}

@media screen and (max-width:800px) {

  .navigation {
    height: 40px;
  }
  #close-menu {
    position: relative;
    top: -100px;
    right: -39%;
    z-index: 100;
    pointer-events: all;
    opacity: 100%;
  }

  .home, .server, .user {
    margin-right: 0;;
  }

  #open-menu {
    z-index: 97;
    color: white;
    display: block;
    overflow: hidden;
    position: absolute;
    right: 45px;
    top: 13px;
  }

  .nav-item {
    line-height: 50px;
  }

  .nav-list {
    z-index: 99;
    position: fixed;
    background-color: #151515;
    width: 100%;
    height: 100%;
    text-align: center;
    right: -100%;
    transition: all 0.5s ease-in-out;
    top: 0%;
  }

  .nav-list {
    flex-direction: column;
  }
}
